<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-基础用法1">基础用法</a></li><li><a href="#vue-doc-尺寸1">尺寸</a></li><li><a href="#vue-doc-禁用1">禁用</a></li><li><a href="#vue-doc-可清空1">可清空</a></li><li><a href="#vue-doc-分组1">分组</a></li><li><a href="#vue-doc-自定义模板1">自定义模板</a></li><li><a href="#vue-doc-多选1">多选</a></li><li><a href="#vue-doc-可搜索1">可搜索</a></li><li><a href="#vue-doc-远程搜索1">远程搜索</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-select选择器1" class="vue-doc-element">Select 选择器</h1>
<p class="vue-doc-element">使用模拟的增强下拉选择器来代替浏览器原生的选择器。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-button1" class="vue-doc-element">Button</h1>
<p class="vue-doc-element">Basic component that is used to trigger bussiness logic.</p>

        </div>

        <div class="vue-doc-components">
          <demobasic></demobasic>
<demosize></demosize>
<demodisabled></demodisabled>
<democlearable></democlearable>
<demogroup></demogroup>
<democustom></democustom>
<demoselect></demoselect>
<demosearch></demosearch>
<demoremote></demoremote>
        </div>


        <div class="vue-doc-content">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<h3 id="vue-doc-selectprops1_1" class="vue-doc-element">Select props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>指定选中项目的 value 值，可以使用 v-model 双向绑定数据。单选时只接受 String 或 Number，多选时只接受 Array</td>
<td>String / Number / Array</td>
<td>空</td>
</tr>
<tr>
<td>multiple</td>
<td>是否支持多选</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>clearable</td>
<td>是否可以清空选项，只在单选时有效</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>filterable</td>
<td>是否支持搜索</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>remote</td>
<td>是否使用远程搜索</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>remote-method</td>
<td>远程搜索的方法</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>loading</td>
<td>当前是否正在远程搜索</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>loading-text</td>
<td>远程搜索中的文字提示</td>
<td>String</td>
<td>加载中</td>
</tr>
<tr>
<td>label</td>
<td>仅在 remote 模式下，初始化时使用。因为仅通过 value 无法得知选项的 label，需手动设置。</td>
<td>String / Number / Array</td>
<td>空</td>
</tr>
<tr>
<td>size</td>
<td>选择框大小，可选值为large、small、default或者不填</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>placeholder</td>
<td>选择框默认文字</td>
<td>String</td>
<td>请选择</td>
</tr>
<tr>
<td>not-found-text</td>
<td>当下拉列表为空时显示的内容</td>
<td>String</td>
<td>无匹配数据</td>
</tr>
<tr>
<td>label-in-value</td>
<td>在返回选项时，是否将 label 和 value 一并返回，默认只返回 value</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>placement</td>
<td>弹窗的展开方向，可选值为 bottom 和 top，2.12.0 版本开始支持自动识别</td>
<td>String</td>
<td>bottom</td>
</tr>
<tr>
<td>transfer</td>
<td>是否将弹层放置于 body 内，在 Tabs、带有 fixed 的 Table 列内使用时，建议添加此属性，它将不受父级样式影响，从而达到更好的效果</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>element-id</td>
<td>给表单元素设置 id，详见 Form 用法。</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-selectevents1_2" class="vue-doc-element">Select events</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>选中的Option变化时触发，默认返回 value，如需返回 label，详见 label-in-value 属性</td>
<td>当前选中项</td>
</tr>
<tr>
<td>on-query-change</td>
<td>搜索词改变时触发</td>
<td>query</td>
</tr>
<tr>
<td>on-clear</td>
<td>点击清空按钮时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-open-change</td>
<td>下拉框展开或收起时触发</td>
<td>true / false</td>
</tr>
<tr>
<td>on-select</td>
<td>手动选中的Option变化时触发</td>
<td>当前选中项</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-selectmethods1_3" class="vue-doc-element">Select methods</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>setQuery</td>
<td>设置搜索词，为空时清空，仅在 filterable=&quot;true&quot; 时有效</td>
<td>query</td>
</tr>
<tr>
<td>clearSingleSelect</td>
<td>清空单选项，仅在 clearable=&quot;true&quot; 时有效</td>
<td>无</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-optionprops1_4" class="vue-doc-element">Option props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>选项值，默认根据此属性值进行筛选，必填</td>
<td>String / Number</td>
<td>无</td>
</tr>
<tr>
<td>label</td>
<td>选项显示的内容，默认会读取 slot，无 slot 时，优先读取该 label 值，无 label 时，读取 value。当选中时，选择器会显示 label 为已选文案。大部分情况不需要配置此项，直接写入 slot 即可，在自定义选项时，该属性非常有用。</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用当前项</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-optiongroupprops1_5" class="vue-doc-element">OptionGroup props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>分组的组名</td>
<td>String</td>
<td>空</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th>Attribute</th>
<th style="text-align:right">Explanation</th>
<th>Type</th>
<th>Default</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td style="text-align:right">Button type, options include primary、ghost、dashed、text、info、success、warning、error, optional</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
import demosize from './demo/size.md';
import demodisabled from './demo/disabled.md';
import democlearable from './demo/clearable.md';
import demogroup from './demo/group.md';
import democustom from './demo/custom.md';
import demoselect from './demo/select.md';
import demosearch from './demo/search.md';
import demoremote from './demo/remote.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,demosize,demodisabled,democlearable,demogroup,democustom,demoselect,demosearch,demoremote
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


