                    setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
                        <span class="hljs-keyword">this</span>.loading1 = <span class="hljs-literal">false</span>;
                        <span class="hljs-keyword">const</span> list = <span class="hljs-keyword">this</span>.list.map(<span class="hljs-function"><span class="hljs-params">item</span> =&gt;</span> {
                            <span class="hljs-keyword">return</span> {
                                <span class="hljs-attr">value</span>: item,
                                <span class="hljs-attr">label</span>: item
                            };
                        });
                        <span class="hljs-keyword">this</span>.options1 = list.filter(<span class="hljs-function"><span class="hljs-params">item</span> =&gt;</span> item.label.toLowerCase().indexOf(query.toLowerCase()) &gt; <span class="hljs-number">-1</span>);
                    }, <span class="hljs-number">200</span>);
                } <span class="hljs-keyword">else</span> {
                    <span class="hljs-keyword">this</span>.options1 = [];
                }
            },
            remoteMethod2 (query) {
                <span class="hljs-keyword">if</span> (query !== <span class="hljs-string">''</span>) {
                    <span class="hljs-keyword">this</span>.loading2 = <span class="hljs-literal">true</span>;
                    setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
                        <span class="hljs-keyword">this</span>.loading2 = <span class="hljs-literal">false</span>;
